<script lang="babel">
  export default {
    name: 'agentAddBase',
    data () {
      return {
        ishas: true,
        ishas1: true,
        dialogFormVisible: false,
        password: '',
        query: {
          mobile: '',
          name: '',
          loginName: '',
          loginPwd: '',
          email: '',
          belongProvinceCode: '',
          belongProvinceName: '',
          belongCityCode: '',
          belongCityName: '',
          belongArea: '',
          bankCard: '',
          bankAccountName: '',
          bankReserveMobile: '',
          idCard: '',
        },
        id: 0,
        isShow: true,
        productId: '',
        images: [],
        fileList: [],
        fileList1: []
      }
    },
    created: function () {
      //
      if (this.$route.query.id != undefined) {
        this.$data.isShow = false;
        this.$http.get('/admin/dealer/findBydealerId/' + this.$route.query.id)
          .then(function (res) {
            this.$data.query = res.data;
            this.$data.province = res.data.belongProvinceName;
            this.$data.city = res.data.belongCityName;
          })
      }
      this.$data.level = this.$route.query.level;
    },
    methods: {
      //
      handleSuccess: function () {
        //
        setTimeout(function () {
          var aSpan = document.getElementById('phone').getElementsByTagName('span')[0];
          document.getElementsByClassName('el-draggeer__cover__btns')[0].removeChild(aSpan)
        }, 300)
      },
      handleSuccess1: function (response, file, fileList) {
        //
        setTimeout(function () {
          var aSpan = document.getElementById('phone1').getElementsByTagName('span')[0];
          if (document.getElementsByClassName('el-draggeer__cover__btns').length == 1) {
            document.getElementsByClassName('el-draggeer__cover__btns')[0].removeChild(aSpan)
          } else {
            document.getElementsByClassName('el-draggeer__cover__btns')[1].removeChild(aSpan)
          }

        }, 300)
      },
      //
      handlePreview: function (file) {
        console.log(file)

        var mask = document.getElementById('mask'),
          img = mask.getElementsByTagName('img')[0];
        img.src = file.url;
        mask.style.display = 'block'

      },
      isNo: function () {
        document.getElementById('mask').style.display = 'none'
      },
      //
      resetPw: function () {
        this.$http.post('/admin/dealer/updatePwd', {dealerId: this.$route.query.id, loginPwd: this.$data.password})
          .then(function (res) {
            this.$data.dialogFormVisible = false;
            this.$data.password = '';
            this.$message({
              showClose: true,
              type: 'success',
              message: ''
            });
          })
          .catch(function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
        /*this.$prompt('', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         }).then(({ value }) => {
         this.$http.post('/admin/dealer/updatePwd',{dealerId:this.$route.query.id,loginPwd:value})
         .then(function (res) {
         this.$message({
         showClose: true,
         type: 'success',
         message: ''
         });
         })
         .catch(function (err) {
         this.$message({
         showClose: true,
         message: err.statusMessage,
         type: 'error'
         })
         })

         }).catch(() => {
         this.$message({
         type: 'info',
         message: ''
         });
         });*/
      },
      //
      create: function () {
        this.$http.post('/admin/user/addFirstDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/admin/record/agentListFir')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      goBack: function () {
        if (this.$route.query.level == 2) {
          this.$router.push('/admin/record/agentListSec')
        } else {
          this.$router.push('/admin/record/agentListFir')
        }
      },
      //
      change: function () {
        this.$data.query.dealerId = this.$data.query.id;
        this.$http.post('/admin/user/updateDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            if (this.$route.query.level == 2) {
              this.$router.push('/admin/record/agentListSec')
            } else {
              this.$router.push('/admin/record/agentListFir')
            }
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      }
    },
  }
</script>

